var events = [];
export function useEventsBroadcaster() {
    function subscribe(event, fn) {
        if (!events[event]) {
            events[event] = [];
        }
        events[event].push(fn);
    }
    function unsubscribe(event, fn) {
        if (!events[event])
            return;
        events[event] = events[event].filter(function (cb) { return cb !== fn; });
    }
    function broadcast(event, data) {
        if (!events[event])
            return;
        events[event].forEach(function (cb) { return cb(data); });
    }
    return ({ subscribe: subscribe, unsubscribe: unsubscribe, broadcast: broadcast });
}
