import { useRouter } from "../hooks/useRouter";
import { useEventsBroadcaster } from "../hooks/useEventsBroadcaster";
import { routes } from "./page-routes";
import { Home } from "../pages/home";
import { About } from "../pages/about";
var router = useRouter();
var broadcaster = useEventsBroadcaster();
var contentRoot = undefined;
var state = {
    currentRoute: undefined,
    defaultRoute: "home"
};
var handleChange = function (route) {
    if (!contentRoot)
        return;
    var defaultRoute = state.defaultRoute, currentRoute = state.currentRoute;
    var routeName = route.split("/")[1] || defaultRoute;
    if (currentRoute === routeName)
        return;
    contentRoot.replaceChildren();
    broadcaster.broadcast("navigation-begin", { currentRoute: currentRoute, nextRoute: routeName });
    switch (routeName) {
        case "about":
            contentRoot.appendChild(About(routes["about"]));
            break;
        default:
            contentRoot.appendChild(Home(routes["home"]));
    }
    var prevRoute = state.currentRoute;
    state.currentRoute = routeName;
    broadcaster.broadcast("navigation-end", { currentRoute: routeName, prevRoute: prevRoute });
};
export function init(ref) {
    contentRoot = ref;
    router.initRouter(handleChange);
}
